<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        @change="changeService"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-select
        v-model="instanceValue"
        placeholder="请选择您的实例"
        size="small"
        @change="changeInstance"
        filterable
        clearable
        style="width: 200px"
      >
        <el-option
          v-for="(item, index) in instanceOptions"
          :key="index"
          :label="item.instanceId.split('#')[0]"
          :value="item.instanceId"
        >
        </el-option>
      </el-select>
    </template>

    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <strong>时间：</strong>
        <span style="float: right; padding: 3px 0">{{
          serverData.crt_time
        }}</span>
      </div>

      <el-row>
        <div>系统平均负荷(%):</div>
        <div style="float: right; padding: 3px 0">
          {{ serverData.me_mean_me_load }}
        </div>
      </el-row>
      <el-row>
        <div>系统最大负荷(%):</div>
        <div style="float: right; padding: 3px 0">
          {{ serverData.me_max_me_load }}
        </div>
      </el-row>
      <el-row>
        <div>UPF虚拟CPU使用率(%):</div>
        <div style="float: right; padding: 3px 0">
          {{ serverData.upf_vcpu_usage }}
        </div>
      </el-row>
      <el-row>
        <div>UPF虚拟Memory使用率(%):</div>
        <div style="float: right; padding: 3px 0">
          {{ serverData.upf_vmemory_usage }}
        </div>
      </el-row>

      <el-row>
        <div>UPF虚拟Disk使用率(%):</div>
        <div style="float: right; padding: 3px 0">
          {{ serverData.upf_vdisk_usage }}
        </div>
      </el-row>
    </el-card>
  </d2-container>
</template>

<script>
import {
  fetchGetServiceList,
  fetchGetInstanceList,
  fetchGetPhyload,
} from "@/api/demo/index";
export default {
  name: "performUldlthp",
  data() {
    return {
      serviceName: "",
      serviceOptions: [],
      instanceOptions: [], //upf下面的实例
      instanceValue: "", //当前选中的实例
      isFirst: true,
      loading: false,
      serverData: {},
    };
  },
  mounted() {
    this.getService();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getServiceInstance(this.serviceName);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 选择服务
    changeService(val) {
      if (!val) {
        return;
      }
      this.getServiceInstance(val);
    },
    // 选择实例
    changeInstance(val) {
      // 图表置空
      this.serverData = {};

      // 获取告警数据
      this.getData();
    },
    //获取服务下面的实例
    getServiceInstance(serviceName) {
      fetchGetInstanceList({ serviceName: serviceName })
        .then((res) => {
          this.instanceOptions = res.hosts;
          if (this.instanceOptions.length > 0) {
            this.instanceValue = this.instanceOptions[0].instanceId;
            this.changeInstance(this.instanceValue);
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取数据
    getData() {
      this.loading = true;
      fetchGetPhyload({
        request_type: "phyload perform",
        instance_id: this.instanceValue,
        sequence_no: 3359,
      })
        .then((response) => {
          this.loading = false;
          console.log(response);
          if ((response != null) & (response != [])) {
            this.serverData = response;
          }
        })
        .catch((e) => {
          this.loading = false;
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
  },
};
</script>

